import React from 'react';
import PropTypes from 'prop-types';
import Preloader from '../preloader/preloader.jsx';
import {getMistakesEnding, getTimeEnding} from '../../utils';

const SECONDS_PER_MINUTE = 60;

const WelcomeScreen = (props) => {
  const {settings, isLoading, onClick} = props;
  const {time, mistakes} = settings;
  const minutes = time / SECONDS_PER_MINUTE;

  return (
    <section className="welcome">
      <div className="welcome__logo">
        <img src="img/melody-logo.png" alt=" " width="186" height="83" />
      </div>
      {!isLoading ?
        (
          <button className="welcome__button" onClick={onClick}>
            <span className="visually-hidden"> </span>
          </button>
        ) :
        (<Preloader />)
      }
      <h2 className="welcome__rules-title"> </h2>
      <p className="welcome__text"> :</p>
      <ul className="welcome__rules-list">
        <li> {minutes} {getTimeEnding(minutes)}     .</li>
        <li>  {mistakes} {getMistakesEnding(mistakes)}.</li>
      </ul>
      <p className="welcome__text">!</p>
    </section>
  );
};

WelcomeScreen.propTypes = {
  isLoading: PropTypes.bool.isRequired,
  settings: PropTypes.exact({
    time: PropTypes.number.isRequired,
    mistakes: PropTypes.number.isRequired
  }),
  onClick: PropTypes.func.isRequired
};

export default WelcomeScreen;
