import React from 'react';
import PropTypes from 'prop-types';
import {parseTime, getTimeEnding, getMistakesEnding} from '../../utils';

const WinScreen = (props) => {
  const {gameTime, mistakes, points, fastPoints, renderButton} = props;
  const {minutes, seconds} = parseTime(gameTime);

  return (
    <section className="result">
      <div className="result__logo">
        <img src="img/melody-logo.png" alt=" " width="186" height="83" />
      </div>
      <h2 className="result__title">  !</h2>
      <p className="result__total">
        {` ${minutes} ${getTimeEnding(minutes)}  ${seconds} ${getTimeEnding(seconds)}   ${points}  (${fastPoints} ),  ${mistakes} ${getMistakesEnding(mistakes)}`}
      </p>
      <p className="result__text">  2   10.     80% </p>
      {renderButton()}
    </section>
  );
};

WinScreen.propTypes = {
  gameTime: PropTypes.number.isRequired,
  mistakes: PropTypes.number.isRequired,
  points: PropTypes.number.isRequired,
  fastPoints: PropTypes.number.isRequired,
  renderButton: PropTypes.func.isRequired
};

export default WinScreen;
